import { Navigate } from 'react-router-dom';
import { useAuth } from 'hooks/useAuth';

export const PublicRoute = ({ component: Component, redirectTo = '/' }) => {
  const { isLoggedIn } = useAuth();

  if (isLoggedIn) {
    return <Navigate to="/phonebook" />;
  }

  return Component;
};
